import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
// import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {NavLink } from 'react-router-dom';

import clsx from "clsx";

import Drawer from "@material-ui/core/Drawer";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    // marginBottom: '64px'
  },
  menuButton: {
    marginRight: theme.spacing(1),
  },
  title: {
       display: 'flex',
    // flexGrow: 1,
    marginLeft: '55%',
    // marginRight: '10%',
    gap:'30%'
//    '&:*':{
  
//    }
    
    
  },
  appbar:{
   
      backgroundColor: '#070707',
        color: '#c4c4c4',
        
           

  },
  links:{
      color:'#c4c4c4',
      textDecoration: 'none',
    //   '&:focus':{
    //     color: '#f0860c',
    //   },
    //   '&:active':{
    //     color: '#f0860c',
    //   }
     
  },
 
  list: {
    width: 250,
    background: '#070707',
    height: "100%",
    color: "#c4c4c4"
  },
  fullList: {
    width: "auto",
    background: "#4c4c4c"
  },
    

}));

const activeLinks={
    color: '#f0860c'
}

export default function ButtonAppBar({home,onButtonClick,skills,projects,contact}) {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false
  });
  


  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === "top" || anchor === "bottom"
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {["Home", "About", "Project", "Contact"].map((text, index) => (
          <ListItem button key={text}>
           
            <ListItemText primary={text} onClick={()=>{window.scrollTo(0,0)}} />
          </ListItem>
        ))}
      </List>
      <Divider />
    </div>
  );



  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appbar}>
        <Toolbar>
        {!home?<IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
        <div>
      {["left"].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}> <MenuIcon className={classes.links} /></Button>
          <Drawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
          >
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
         
          </IconButton>
            :
          <Typography variant="h6" className={classes.title}>

            <NavLink to="/home" className={classes.links} activeStyle={activeLinks} onClick={()=>{window.scrollTo(0,0)}}>Home</NavLink >
            <NavLink to="/about"   className={classes.links} activeStyle={activeLinks} onClick={()=>{onButtonClick(skills)}}>About</NavLink >
           <NavLink to="/project"  className={classes.links} activeStyle={activeLinks} onClick={()=>{onButtonClick(projects)}}>Project</NavLink >
            <NavLink to="/contact"   className={classes.links} activeStyle={activeLinks} onClick={()=>{onButtonClick(contact)}}>Contact</NavLink >
    </Typography>
    
    }
          
         
   
         
        
        </Toolbar>
      </AppBar>
    </div>
  );
}








